import React from 'react'
import { Col, Container } from 'react-bootstrap'
import logo from "../assets/img/logo.svg"
import navIcon1 from '../assets/img/nav-icon1.svg';
import navIcon3 from '../assets/img/nav-icon3.svg';

function Footer() {
  return (
    <footer className='footer'>
        <row className='align-item-center px-1' style={{border: '1px solid green'}}>
            
            <Col size={12} className='text-center text-sm-center'>
                <div className='social-icon'>
                <a href='https://www.linkedin.com/in/abiloye-mogbeyiteren-412909286'><img src={navIcon1} alt=''/></a>
                    
                    <a href='https://www.instagram.com/bilo_ye/'><img src={navIcon3} alt=''/></a>
                </div>
                <p>Abiloye Blossom, 2023</p>
                
            </Col>
           
            
        </row>

    </footer>
  )
}

export default Footer