import React from 'react'
import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/project-img1.png";
import projImg2 from "../assets/img/project-img2.png";
import projImg3 from "../assets/img/project-img3.png";
import projImg4 from "../assets/img/project-img5.png";
import colorSharp2 from "../assets/img/color-sharp2.png";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

function Projects() {
  const projects =[
    {
        title: "Web-Based Voting System",
        description: "A web-based implementation on an online voting system using ReactJs and NodeJs",
        imgUrl: projImg2,
        category: 'Web Development',
         position: 1

  },
  {
    
    title: "Web-Based Course Registration System",
    description: "A web-based implementation on an online course registration system using html,css,javascript for the frontend and PHP for the backend",
    imgUrl: projImg1,
    category: 'Web Development',
    position: 1

  },
  {
    
    title: "ML Project",
    description: "A simple machine learning project implemented with python on various popular datasets such as the iris dataset",
    imgUrl: projImg3,
    category: 'Machine Learning',
    position: 2

  },
  {
    
    title: "AI ChatBot using OpenAI",
    description: "A web implementation of an AI chatbot using the OpenAI platform api keys and gpt-3.5 model for chat completion",
    imgUrl: projImg4,
    category: 'AI and Web Development',
    position: 2

  }

]
  return (
    <section className='project' id='projects'>
        <Container>
            <Row>
                <Col size={12}>
                <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                      <Tab.Container id="projects-tabs" defaultActiveKey="first">
                  <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                    <Nav.Item>
                      <Nav.Link eventKey="first">Tab 1</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="second">Tab 2</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="third">Others</Nav.Link>
                    </Nav.Item>
                  </Nav>
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    
                    <Tab.Pane eventKey="first">
                      <p>Web Development Projects</p>
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                Position={1}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="second">
                      <p>Machine Learning and AI Projects</p>
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                Position={2}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                      <p>Coming Soon</p>
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    

                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
                
                </Col>
            </Row>
        </Container>
    <img className='background-image-right' src={colorSharp2}></img>
    </section>
  )
}

export default Projects