import React, { useEffect, useState } from 'react'
import { Container, Row, Col } from "react-bootstrap";
import {ArrowRightCircle} from "react-bootstrap-icons";
import 'bootstrap/dist/css/bootstrap.min.css';
import { TypeAnimation } from 'react-type-animation';
import headerImg from "../assets/img/header-img2.svg";

export default function Banner() {

  return (
   <section className='banner' id='home'>
    <Container>
        <Row className="align-items-center">
            <Col xs={12} md={6} xl={7}>
                <span className='tagline'>
                    Welcome to my Portfolio
                </span>
                <h1>{`Hi I'm Abiloye Blossom`}</h1>
                <h2>
                <span className='wrap'>
                    <TypeAnimation
    preRenderFirstString={false}
    sequence={[
      1000,
      "Web Developer", // initially rendered starting point
      2000,
      "Web Designer",
      2000,
      "AI and Machine Learning Enthusiast",
      500,
    ]}
    speed={20}
    
    repeat={Infinity}
  /></span>
  </h2>
                

                
                
                <p>
                    
                As a web developer, I specialize in React for building dynamic and user-friendly interfaces. I also have experience with Node.js, allowing me to create powerful and scalable server-side applications. In addition, I dabble in Python for machine learning, exploring the fascinating world of AI.

                With a keen interest in artificial intelligence, I'm constantly seeking opportunities to blend my web development skills with AI technologies. My passion for AI fuels my curiosity to experiment and learn more about cutting-edge advancements in the field.

                Overall, I am a versatile developer with a focus on React, Node, and a touch of Python for machine learning, eager to explore the exciting possibilities that AI offers in the realm of web development.
                </p>
                <button> Let's Connect <a href='#connect'><ArrowRightCircle size={25}></ArrowRightCircle></a></button>
            </Col>
            <Col xs={12} md={6} xl={5} style={{ height: 350}} >
                
                <img src={headerImg} style={{ height:'100%', width:'100%'}} alt="Header Img"/>
                
                
                
            </Col>
        </Row>
    </Container>
   </section>
  )
}
