import React, { useState } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Button, Col, Container, Row } from 'react-bootstrap';
import contactImage from '../assets/img/contect-img2.svg'




function Contact() {
  const formInitialDetails = {
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    message: ''

  }
  const [formDetails, setFormDetails]=useState(formInitialDetails);
  const [buttonText, setButtonText]= useState('Send');
  const [status, setStatus]= useState({});
  const onFormUpdate = (category, value) => {
    setFormDetails({
        ...formDetails,
        [category]: value
    })

  }
  const handleSubmit = async (e) => {
    e.preventDefault();
    setButtonText("Sending...");
    let response = await fetch("https://mailserver-c6x4.onrender.com/contact", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
      },
      body: JSON.stringify(formDetails),
    });
    setButtonText("Send");
    let result = await response.json();
    setFormDetails(formInitialDetails);
    if (result.code == 200) {
      setStatus({ success: true, message: 'Message sent successfully'});
    } else {
      setStatus({ success: false, message: 'Something went wrong, please try again later.'});
    }
  };
  const handleToast = () => {
    if (status.success === false) {
        toast.error(status.message);
    } else if (status.success === true) {
        toast.success(status.message);
    }
};

  
  return (
    <section className='contact' id='connect'>
        <Container>
            <Row className='align-items-center'>
                <Col md={6}>
                    <img src={contactImage} alt='Contact Us'/>
                </Col>
                <Col md={6}>
                    <h2>Get in touch</h2>
                    <form onSubmit={handleSubmit}>
                        <Row>
                            <Col sm={6} className="px-1">
                                <input type="text" value={formDetails.firstName} placeholder='First Name' onChange={(e)=> onFormUpdate('firstName', e.target.value)}></input>
                            </Col>
                            <Col sm={6} className="px-1">
                            <input type="text" value={formDetails.lastName} placeholder='Last Name' onChange={(e)=> onFormUpdate('lastName', e.target.value)}></input>

                            </Col>
                            <Col sm={6} className="px-1">
                            <input type="email" value={formDetails.email} placeholder='Email Address' onChange={(e)=> onFormUpdate('email', e.target.value)}></input>

                            </Col>
                            <Col sm={6} className="px-1">
                            <input type="tel" value={formDetails.phone} placeholder='Phone Number' onChange={(e)=> onFormUpdate('phone', e.target.value)}></input>

                            </Col>
                            <Col sm={6} className="px-1">
                           <textarea rows={6} type="message" value={formDetails.message} placeholder='Message' onChange={(e)=> onFormUpdate('message', e.target.value)}></textarea>
                            <Button type='submit' className='tagline'><span>{buttonText}</span></Button>
                            </Col>
                            {
                                
                                <Col>
                                <ToastContainer />
                                {status.message && handleToast()}
                                </Col>

                            }
                        </Row>
                    </form>
                </Col>

            </Row>
            
                

            
        </Container>

    </section>
  )
}

export default Contact